section{
    overflow-x: hidden;
}

.top{
    background: #292929;
    width: 1440px;
    height: 140px;
}

.texts{
    background-color: #292929;
    border: solid #fff 1px;
    width: 193px;
    height: 60px;
    color: #fff;
    margin: 2rem 6rem;
    text-align: center;
    font-size: 1.7rem;
}
.background{
    background-image: url(../assest/Rectangle\ 5.png);
    background-size: cover;
    height: 550px;
    width: 1440px;
    margin-top: -4.5rem;
}
.text{
    padding: 60px 77px;
    width: 490px;
    height: 282px;
    color: #fff;
    font-family: DM Sans;
    font-size: 72px;
    font-weight: 700;
    line-height: 94px;
    letter-spacing: -0.05em;
    text-align: left;

}
h3{
    margin: 1.5rem 1.5rem;
    font-weight: 500;
}
input{
    border: solid #000 1.5px;
    width: 1300px;
    height: 54px;
    margin: .2rem 2rem;
}

.movies{
    overflow: hidden;
    height: 20rem;
    margin: 2.5rem 1rem;
    display: flex;
    align-items: center;
    justify-content: space-between;

}
.cards{ 
    margin: 0 1.5rem;
    height: 60rem;
    flex-wrap: wrap;
    width: 50rem;
    left: 0px;
    top: 0px;
    padding: 10rem;
    border-radius: 12px;
    background: #000;
}

@media (max-width: 1000px) {
    .texts{
        justify-content: center;
        margin: 3rem 10rem;
    }
    .text{
        text-align: center;
        font-size: 50px;
        font-weight: 400;
        align-items: center;
        }
}